// src/app/page.tsx
import { Parallax } from "@react-spring/parallax";
import HeroSection from "../components/HeroSection";
import AboutSection from "../components/AboutSection";
import ProjectsSection from "../components/ProjectsSection";

export default function Home() {
  // We define the total number of "pages" needed for all three sections.
  // Hero (1 page) + About (1 page) + Projects (1 page) = 3 pages total.
  const totalPages = 3;

  return (
    // The Parallax component replaces the scrollable main tag
    // The className "scroll-smooth" is no longer needed as Parallax handles scroll.
    <Parallax pages={totalPages} style={{ top: "0", left: "0" }}>
      {/*
        1. HeroSection: Starts at offset={0} (the first page)
        2. AboutSection: Starts at offset={1} (the second page)
        3. ProjectsSection: Starts at offset={2} (the third page)
        
        Note: The actual ParallaxLayer wrapper is inside the components for cleaner code.
        We pass the layer props (offset, speed) to each component.
      */}
      <HeroSection offset={0} speed={0.5} />
      <AboutSection offset={1} speed={0.5} />
      {/* You would create a ProjectsSection component similarly, 
          assuming it would take up the third page. */}
      {/* <ProjectsSection offset={2} speed={0.5} /> */}

    </Parallax>
  );
}